import React, { useState, useEffect } from 'react';
import { Button, FormControl, InputLabel, Input } from '@material-ui/core';
import './App.css';
import Message from './Message';
import db from './firebase';
import firebase from 'firebase';
import FlipMove from 'react-flip-move';
import SendIcon from '@material-ui/icons/Send';
import { IconButton } from '@material-ui/core';


function App() {

  //Statekn√©l nem kell friss√≠teni az oldalt, az√©rt jobb mint a v√°ltoz√≥k
  const [input, setInput] = useState('');
  const [messages, setMessages] = useState([]);
  const [username, setUsername] = useState('');

  useEffect(() => {
    //Run code here
    // Ha a [] √ºres, akkor ez a k√≥d amit itt √≠runk EGYSZER fog lefutni amikor a komponens bet√∂lt
    setUsername(prompt('Please Enter Your Name')) 
  }, [] ) //Condition

  useEffect(() =>{
    //Egyszer fut le amikor a komponens bet√∂lt
    //onSnapshot -> ha ak√°rmilyen v√°ltoz√°st tal√°l beledobja a snapshotba
    // A doc a adatbazison bel√ºli document-ek
    //a map-al bej√°rjuk √©s kiszedj√ºk a dokumentumokban l√©v≈ë adatokat
    db.collection('messages').orderBy('timestamp', 'desc').onSnapshot(snapshot => {
      setMessages(snapshot.docs.map(doc => ({id: doc.id, message: doc.data()})))
    }); 
  }, [])

  const sendMessage = (event) => {
    //...messages -> tartsd meg az √∂sszes alap messaget, √©s tedd hozz√° pluszba az inputban l√©v≈ëket!!!
    event.preventDefault();
    db.collection('messages').add({
      message: input,
      username: username,
      timestamp: firebase.firestore.FieldValue.serverTimestamp()
    })

    setMessages([...messages, {username: username, message: input}]);
    setInput('');
  }

  return (
    <div className="App">
      <img src="https://image.flaticon.com/icons/svg/2950/2950657.svg" width="10%" />
        <h1 className="welcomeText">Szia {username}!  Kezdj el chatelni. ü§ô </h1>
      {/* Form √∫jra t√∂lri az oldalt, ami nem j√≥ nek√ºnk 
      event.preventDefault(); Ezzel m≈±k√∂dik */}
      <form className="app__form">
        <FormControl className="app__formControl center">
          <InputLabel>Enter a message...</InputLabel>
          <Input className="app__input" value={input} onChange={event => setInput(event.target.value)} />


          <IconButton className="app__iconButton" disabled={!input}  
            variant="contained" 
            color="primary" 
            type='submit' 
            onClick={sendMessage}>
              <SendIcon></SendIcon>
          </IconButton>

          {/* type=submit --> enterrel is megy */}
        </FormControl>
      </form>


      <FlipMove className="messages__box">
      {
        //Bej√°rja a messages t√∂mb√∂t, √©s minden egyes elem√©t ki√≠rja!
        messages.map(({id, message}) => (
          <Message key={id} username={username}  message={message}/>
        ))
      }
      </FlipMove>
      
    </div>
  );
}

export default App;
